<template>
  <div class="position-relative w-100 min-vh-75 overflow-hidden bg-gray-light">
    <!-- primary -->
    <div class="position-absolute w-25 h-100 top-0">
      <img
        src="@/assets/imgs/version02/home-shape-primary.svg"
        :class="$style.shapePrimary"
        alt=""
      >
    </div>
    <!-- tertiary -->
    <img
      src="@/assets/imgs/version02/home-shape-tertiary.svg"
      class="w-40 h-auto position-absolute object-position-left top-0 end-0"
      :class="$style.shapeTertiary"
      alt=""
    >
    <!-- quaternary -->
    <img
      src="@/assets/imgs/version02/home-shape-quaternary.svg"
      class="w-20 h-auto position-absolute object-position-left top-0 end-0"
      :class="$style.shapeQuaternary"
      alt=""
    >
    <div class="position-relative container">
      <div class="row min-vh-75 align-items-center justify-content-center">
        <div class="col-12 col-md-8 col-lg-6">
          <h1 class="mb-3">
            <span
              class="d-inline-block me-3"
              :class="$style.popUpAnimation"
            >Hello!</span>
            <span
              class="d-inline-block"
              :class="$style.opacityAnimation"
            > I’m Rita.</span>
          </h1>
          <p
            id="home-description"
            class="text-tertiary"
            :class="$style.homeDescription"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { onMounted } from '@vue/runtime-core'
export default {
  name: 'VersionView02Home',
  setup () {
    // 打字效果
    let count = 0
    const content = `I’m a front-end engineer with 3 years experience, mostly work with Vue.js.<br>
            Good at transforming layout to website, also praised for being committed and meticulous.`

    const typingEffect = () => {
      if (count <= content.length && document.getElementById('home-description')) {
        document.getElementById('home-description').innerHTML = content.substring(0, count)
        count++
        setTimeout(() => typingEffect(), 30)
      }
    }

    onMounted(() => {
      // wait for css animation done
      setTimeout(() => typingEffect(), 1200)
    })
    return { }
  }
}
</script>

<style lang="scss" module>
.shapePrimary {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: right;
}

.shapeTertiary {
  transform: translate(20%, -20%);
}

.shapeQuaternary {
  transform: translate(15%, 100%);
  animation: floating 2s ease-in-out infinite;
}

@keyframes floating {
  0% {
    margin-top: 20px;
  }

  50% {
    margin-top: 0;
  }

  100% {
    margin-top: 20px;
  }
}

.popUpAnimation {
  display: inline-block;
  animation: popup 0.5s ease-in-out;
  transform-origin: left;
}

@keyframes popup {
  0% {
    opacity: 0;
    transform: rotate(-90deg) translateY(20px);
  }

  100% {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

.opacityAnimation {
  animation: opacity-animation 0.5s ease-in-out;
  opacity: 0;
  animation-delay: 0.5s;
  animation-fill-mode: forwards;
}

@keyframes opacity-animation {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.homeDescription {
  min-height: 6rem;
}
</style>
