<template>
  <div class="position-relative w-100 overflow-hidden">
    <!-- secondary -->
    <img
      src="@/assets/imgs/version02/works-shape-secondary.svg"
      class="w-40 h-auto position-absolute object-position-left top-50 start-0"
      :class="$style.shapeSecondary"
      alt=""
    >
    <!-- primary -->
    <img
      src="@/assets/imgs/version02/works-shape-primary.svg"
      class="w-60 h-auto position-absolute object-position-left top-0 end-0"
      :class="$style.shapePrimary"
      alt=""
    >
    <!-- quaternary -->
    <img
      src="@/assets/imgs/version02/works-shape-quaternary.svg"
      class="w-40 h-auto position-absolute object-position-left bottom-0 end-0"
      :class="$style.shapeQuaternary"
      alt=""
    >
    <div class="position-relative container-lg">
      <div class="row align-items-stretch pt-6 pb-5">
        <div class="col-12">
          <h2 class="fw-bold mb-2">
            Meet some projects I had participated in...
          </h2>
          <p class="text-tertiary mb-5">
            Most of projects are develop all by my self, and some of them are designed by me.
          </p>
        </div>
        <div
          v-for="(item, index) in worksList"
          :key="index"
          class="js-show-on-scroll mb-5"
          :class="[worksStyleMap[item.id].isImportant ? 'col-12' : 'col-12 col-lg-6']"
        >
          <div
            class="position-relative container-fluid g-0"
            :class="$style.work"
          >
            <div class="row align-items-center">
              <div :class="[worksStyleMap[item.id].isImportant ? 'col-12 col-lg-6' : 'col-12']">
                <div class="bg-gray-light px-4 pt-4">
                  <div :class="$style.thumbnail">
                    <img
                      :src="require(`@/assets/imgs/works/${item.imgs[0]}`)"
                      alt=""
                      :class="$style.thumbnailImg"
                    >
                  </div>
                </div>
              </div>
              <div :class="[worksStyleMap[item.id].isImportant ? 'col-12 col-lg-6' : 'col-12']">
                <div class="px-2 pt-4">
                  <div class="d-flex mb-2">
                    <h3 class="me-2">
                      {{ item.title }}
                    </h3>
                    <a
                      v-if="item.link"
                      :href="item.link"
                      target="_blank"
                      class="stretched-link"
                      :class="$style.link"
                    >
                      <AppIcon
                        name="link"
                        :class="$style.linkIcon"
                      />
                    </a>
                  </div>
                  <span
                    v-for="(tag, tagIndex) in item.tags"
                    :key="tagIndex"
                    :class="$style.tag"
                  >{{ tag }}</span>
                  <p class="text-tertiary user-select-none mb-3">
                    {{ item.description }}
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { worksList } from '@/helpers/useWorks'
const worksStyleMap = {
  'omniec-content-management': {
    isImportant: true
  },
  'omniec-frontend': {
    isImportant: true
  },
  'omniec-admin': {
    isImportant: false
  },
  'omniec-coolmama': {
    isImportant: false
  },
  'omniec-nana': {
    isImportant: false
  },
  'omniec-atenore': {
    isImportant: false
  }
}

</script>

<style lang="scss" module>
  @import "~@/assets/styles/variables";

  .shapeSecondary {
    transform: translate(-10%, -20%);
  }

  .shapePrimary {
    transform: translate(20%, 15%);
  }

  .shapeQuaternary {
    transform: translate(-10%, 30%);
  }

  .work {
    transition: all 0.3s;

    &:hover {
      box-shadow: 0 0 0.75rem rgba($text, 0.2);
    }

    .thumbnail {
      position: relative;
      padding-bottom: 65%;
      height: 0;

      .thumbnailImg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: top left;
      }
    }

    .link {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 2rem;
      height: 2rem;
      color: $quaternary-light;
      transition: color 0.3s;

      .linkIcon {
        width: 1.5rem;
        height: 1.5rem;
        fill: currentColor;
      }

      &:hover {
        color: $quaternary;
      }
    }

    .tag {
      display: inline-block;
      padding: map-get($spacers, 1) map-get($spacers, 2);
      margin: 0 map-get($spacers, 2) map-get($spacers, 3) 0;
      font-size: $small-font-size;
      background-color: rgba($tertiary, 0.25);
      user-select: none;
      border-radius: $border-radius;
    }
  }
</style>
