
import { computed } from '@vue/runtime-core'
import { useRoute } from 'vue-router'

const versionList = [
  {
    title: '多彩風格',
    titleEnglish: 'Colorful version',
    link: '/'
  },
  {
    title: '淺粉風格',
    titleEnglish: 'Pink version',
    link: '/version-01-light'
  },
  {
    title: '深色風格',
    titleEnglish: 'Dark version',
    link: '/version-01-dark'
  }
]
const versionListFiltered = computed(() => {
  const route = useRoute()
  return versionList.filter((item) => item.link !== route?.path)
})

export {
  versionList, versionListFiltered
}
